/*  Style entire app for particles and scrollview */
.viewstyle {
  overflow: auto;
  height: -webkit-fill-available;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
/*  App-Wide styles for components. Can be overridden by child CSS classes. */
/*  Type-Specific Styling */
input {
  border: 0;
  width: 100%;
  padding: 12px;
  border-radius: 10px;
  box-sizing: border-box; }

input[type=submit] {
  color: white;
  background-color: darkslategray;
  transition: .25s; }

input[type=text] {
  color: black; }

input[type=submit]:hover {
  color: white;
  background-color: green;
  transition: .25s; }

.background-particles {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  z-index: -1337;
  color: grey;
  background-color: slategray; }

/*  Component-Specific Styling */
.App {
  text-align: center;
  color: white; }

.App-logo {
  height: 40vmin;
  max-width: 200px; }

.App-header {
  display: -webkit-flex;
  display: flex;
  min-height: 10vh;
  height: 7.5vh;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  background-color: #252525;
  color: #d1d1d1; }

.App-body {
  display: grid;
  margin: auto;
  -webkit-align-items: center;
          align-items: center;
  max-width: 400px;
  width: 85vw; }

.body-text {
  display: grid;
  max-width: 400px;
  width: 85vw; }

.user-form {
  display: grid;
  max-width: 400px;
  width: 85vw; }

.fill-style {
  width: -webkit-fill-available; }

.login {
  background-color: #f7f5f5;
  padding: 20px;
  height: 350px;
  width: 450px;
  text-align: center;
  line-height: 50px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2); }

.login h1 {
  color: #464545; }

.login h4 {
  font-style: oblique;
  color: #696868; }

td {
  padding: 6px;
  border-radius: 1px;
  line-break: auto; }

button[type='sort'] {
  color: white;
  background-color: #636363;
  border-color: black;
  border-radius: 10px;
  margin: 10px;
  transition: .25s; }

button[type='sort'] :active {
  background-color: darkolivegreen;
  transition: .25s; }

.sort-active {
  background-color: green; }

.track-table {
  overflow-x: hidden;
  display: block;
  opacity: 100;
  margin: 0 auto;
  -webkit-margin-after: 10px;
          margin-block-end: 10px;
  width: 90vw;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  height: 500px;
  -webkit-animation: none 0s ease 0s 1 normal none running-screen-slide-down-empty-full ease-out .5s;
          animation: none 0s ease 0s 1 normal none running-screen-slide-down-empty-full ease-out .5s;
  -webkit-animation: initial-screen-slide-down-empty-full ease-out .5s;
          animation: initial-screen-slide-down-empty-full ease-out .5s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards; }

table {
  overflow: auto;
  opacity: 0;
  -webkit-animation: none 0s ease 0s 1 normal none running-screen-slide-down-empty-full ease-out .5s;
          animation: none 0s ease 0s 1 normal none running-screen-slide-down-empty-full ease-out .5s;
  -webkit-animation: initial-screen-slide-down-empty-full ease-out .5s;
          animation: initial-screen-slide-down-empty-full ease-out .5s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  margin: 0 auto;
  width: 90vw;
  max-width: 420px;
  height: 500px; }

th {
  border-radius: 2px;
  -webkit-transform: translateY(1px);
          transform: translateY(1px);
  text-align: center;
  color: 'green'; }

td {
  border: 1px solid #5e5e5e;
  padding: 10px; }

tr:nth-child(even) {
  background-color: #272727; }

tr {
  background-color: #000000; }

tr:hover {
  background-color: #5a5a5a; }

thead > tr {
  background-color: #005a03; }

tbody {
  height: 75vh; }

.title-text {
  text-align: left;
  text-decoration: none; }

.track-table-item a {
  color: white; }

.track-table thead tr {
  text-align: center; }

.background-particles-alt {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  z-index: -1337;
  height: 10000%;
  width: 10000%;
  color: whitesmoke;
  background-color: black; }

.Analysis {
  color: white;
